import SubMainMenu from '../SubPages/SubMainMenu/SubMainMenu';
import TestsMenu from '../SubPages/TestsMenu/TestsMenu';
import TestsPage from '../SubPages/TestsPage/TestsPage';

import React, { useState, useEffect } from 'react';

import './MainPage.css';
import { useSelector, useDispatch } from 'react-redux';

const MainPage = () => {    
    const dispatch = useDispatch();   
    const curSubPage = useSelector( state => state.STORE_curSubPage);    
    const [ curShowSubPage, setCurShowSubPage ] = useState('');

    let MainNav_clickHandler = ( tt ) => {
        dispatch({
            type : "SWITCH_TO_NAV_MENU_EL", 
            targetPage : tt 
        });
    }
     useEffect (()=> {               
        switch ( curSubPage ) {
            case "sMainMenu" : 
                setCurShowSubPage( <SubMainMenu />);                
                break;
            case "sTestsMenu" : 
                setCurShowSubPage( <TestsMenu />);
                break;
            case "sTest" : 
                setCurShowSubPage( <TestsPage />);
                break;
            default :
                setCurShowSubPage( <SubMainMenu />)
        };        
    },[curSubPage])
    return (
        <div className = "MainPage">
            <button
                onClick = { () => MainNav_clickHandler("sMainMenu")} 
                >
                    MAIN NAV_MAIN_MENU</button>
            <button
                onClick = { () => MainNav_clickHandler("sTestsMenu")} 
                >
                    MAIN NAV_TESTS</button>
           { curShowSubPage }
        </div>
    )
}

export default MainPage;