import "./LoginPage.css";

import { useDispatch } from 'react-redux'; 

const LoginPage = () => {
    const dispatch = useDispatch();  

    const LP_but_handler = () => {
        dispatch({
            type : "LOG_IN"
        });
    }

    return (
        <div className = "LoginPage">
            LP
            <input 
                type = "text"
                className = "LP_login"
                required ></input>
            <input 
                type = "password"
                className = "LP_pass"></input>
            <button
                onClick = {LP_but_handler}>ads</button>
        </div>
    )
}

export default LoginPage;